import { GithubIcon, TwitterIcon, InstagramIcon, MailIcon } from 'vue-feather-icons'

export default {
  name: 'socials',
  methods: {
    getIcon (type) {
      switch (type) {
        case 'github': return GithubIcon
        case 'twitter': return TwitterIcon
        case 'instagram': return InstagramIcon
        case 'mail': return MailIcon
      }
    }
  }
}
